.popup {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  color: #000;
}

.popup_opened {
  opacity: 1;
  visibility: visible;
  transition: visibility 0.3s, opacity 0.3s linear;
}

.popup__container {
  position: relative;
  display: flex;
  flex-direction: column;
}

.popup__container_type_image {
  background-color: #fff;
  overflow: hidden;
  border-radius: 16px;
  width: 496px;
}

.popup__item-img {
  overflow: hidden;
}

.popup__footer {
  padding: 20px 32px 32px;
  display: grid;
  gap: 12px;
}

.popup__text {
  padding: 0;
  margin: 0;
}
