.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 40px;
  width: 100%;

  /* font */
  font-family: 'Cabinet Grotesk Regular', sans-serif;
  font-size: 20px;
  color: #000;
  font-weight: 700;
  line-height: 24px;
}

.header__welcome {
  display: flex;
  align-items: center;
  gap: 24px;
}

.header__profile {
  display: flex;
  align-items: center;
  gap: 20px;
}

.header__profile-add-clothes-btn {
  font-family: 'Cabinet Grotesk Regular', sans-serif;
  font-size: 20px;
  color: #000;
  font-weight: 700;
  line-height: 24px;
  cursor: pointer;

  background: none;
  border: none;
  padding: 0;
}

.header__profile-user-avatar {
  width: 40px;
  height: 40px;
  margin-left: 88px;
}
